@mixin onPhone375 {
  @media (min-width: 375px) {
    @content;
  }
}

@mixin onTablet768 {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onTablet990 {
  @media (min-width: 990px) {
    @content;
  }
}

@mixin onLaptop {
  @media (min-width: 1440px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1920px) {
    @content;
  }
}
